import * as React from 'react'

const Pokedex = props =>{
    return(<>
    <div style={{"font-size":"1.5em"}}>Pokedex UWP App</div>
    <div style={{"font-size":"1em"}}>Page content to come soon!</div>
    <div style={{margin:"1em"}}>For now feel free to visit this project on my Github!</div>
    <div>
    <button className="githubButton" onClick={()=>{window.open("https://github.com/mexvance/PokeGUI", '_blank')}}>PokeGui on Github</button>
    </div>
    </>)
}

export default Pokedex